html {
    min-height: 100vh;
    background-repeat: no-repeat;
    background-image: linear-gradient(#353535, #191919);
}

.sectionWrapper {
    background-image: linear-gradient(to right, #171718, #2c2b2d);
}

.sectionHeading {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align: center;
}

    .sectionHeading h1, #sectionHeading h2, .sectionHeading h3, .sectionHeading h4, .sectionHeading h5 {
        background-image: linear-gradient(#ffd26e, #feda88, #ffd26e);
        background-clip: text;
        color: transparent;
        margin: 5px auto;
    }

.button {
    background-image: linear-gradient(to right, #222224, #4f4a50) !important;
    color: #ffd26e !important;
}

    .button:hover {
        background-image: linear-gradient(to right, #897430, #a9903b) !important;
    }

.smallButton {
    font-size: 0.7825rem !important;
    padding: 2px 5px !important;
}

.mediumButton {
    font-size: 0.8125rem !important;
    padding: 4px 10px !important;
}

.largeButton {
    font-size: 0.8425rem !important;
    padding: 6px 15px !important;
}
